.root {
  background: linear-gradient(90deg, #360033 0%, #0b8793 100%);
  border-radius: 20px;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}
.backgroundOverlay {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: max-content;
  right: 0;
  top: 0;
}
.img {
  height: 100%;
  width: auto;
}
.rakeRoot {
  position: relative;
  z-index: 1;
  width: max-content;
  padding: 20px;
  padding-right: 0;
}
.title {
  font-weight: 1000;
  font-size: 44px;
  line-height: 40px;
  letter-spacing: 0.03em;

  color: #f3daa4;

  margin-bottom: 11px;
}
.text {
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.03em;

  color: #ffffff;
  min-width: 240px;
  @media (max-width: 600px) {
    font-weight: 600;
    font-size: 13px;
    line-height: 16px;
  }
}
