@import "sass/index";
$minSectionHeight: 189px;
.root {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 10px;
  gap: 14px;

  @media (max-width: 400px) {
    padding: 0 7px;
  }
}
.item {
}
.title {
  font-family: "Gilroy";
  font-weight: bold;
  margin: spacing(3.5) spacing(2.75);
  width: 136px;
  font-size: 14px;
  letter-spacing: 1px;

  color: use(--secondary-main);
}
.flex {
  $gap: 10px;

  display: flex;
  flex-wrap: wrap;
  gap: $gap;
  min-height: 189px;
  width: 100%;

  & > * {
    min-width: 315px;
    min-height: $minSectionHeight;

    @media (max-width: 600px) {
      min-width: auto;
      width: 100%;
      min-height: auto;
      height: 165px;
    }
  }
  @media (max-width: 600px) {
    flex-direction: column;
  }
}
.firstLine {
  flex-wrap: wrap-reverse;
  section:first-child {
    flex-grow: 2;
  }

  section:last-child {
    flex-grow: 1;
  }
}
.grid {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(415px, 1fr));
  & > * {
    min-height: $minSectionHeight;
  }
  @media (max-width: 600px) {
    grid-template-columns: 1fr;
    & > * {
      min-height: auto;
      height: 165px;
    }
  }
}
