$padding: 30px;

.root {
  padding: $padding;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.wrapRoot {
  width: 100%;
  position: relative;

  & .link {
    position: absolute;
    right: $padding;
  }
  & .title {
    margin-bottom: 30px;
  }
}
.textWrapper {
  max-width: fit-content;
}

.title {
  margin: 0;
  font-size: 1.375rem;
  color: white;
  font-weight: bold;
  font-family: "Gilroy";
  text-transform: uppercase;
  display: inline-block;
}
.text {
  margin: 0;
  font-size: 1rem;
  color: #6f99eb;
  font-weight: 400;
  font-family: "Varela Round", sans-serif;
  display: inline-block;
}
.link {
  margin: 0;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.03em;
  padding-top: 14px;

  color: #ffffff;

  @media (max-width: 600px) {
    font-size: 12px;
    line-height: 14px;
    padding-top: 8px;
  }
  font-family: "Gilroy";
  text-decoration: none;

  cursor: pointer;
}
