$backgroundUrl: "../../assets/banners/rakeback.png";
$backgroundUrlMobile: "../../assets/banners/rakebackMobile.png";

.root {
  border-radius: 20px;
  background: linear-gradient(
      173.73deg,
      #4e82d6 -75.15%,
      rgba(11, 4, 21, 0.181709) 42.59%,
      rgba(3, 1, 4, 0.0001) 98.76%,
      #000000 140.1%
    ),
    #0a0d18;

  display: flex;
  justify-content: space-between;
  overflow: hidden;
  height: 100%;

  position: relative;
}
.backgroundOverlay {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
  background: url(#{$backgroundUrl});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;

  @media (max-width: 600px) {
    background: url(#{$backgroundUrlMobile});
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: 100%;
  }
}

.rakeRoot {
  height: auto !important;
  position: relative;
  z-index: 1;
  padding: 20px;
  padding-right: 0;
}
.title {
  font-weight: 1000;
  font-size: 44px;
  line-height: 44px;
  letter-spacing: 0.03em;

  color: #fcd517;
  margin-right: 44px;
  margin-bottom: 18px;
  white-space: nowrap;
  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 27px;
    margin-bottom: 7px;
  }
  @media (max-width: 340px) {
    font-size: 28px;
    line-height: 27px;
    margin-bottom: 7px;
  }
  display: inline-block;
}
.text {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.03em;

  width: 80%;

  color: #ffffff;

  @media (max-width: 600px) {
    font-size: 13px;
    line-height: 16px;
    display: inline-block;
  }
}
.link {
  color: #fcd517;
}
