$backgroundUrl: "../../assets/banners/winBonuses.png";
$backgroundUrlMobile: "../../assets/banners/winBonusesMobile.png";

.root {
  border-radius: 30px;
  display: flex;
  overflow: hidden;
  min-height: 290px;
  position: relative;

  @media (max-width: 600px) {
    min-height: 164px;
  }
}
.backgroundOverlay {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
  background: url(#{$backgroundUrl});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  @media (max-width: 768px) {
    background-position: 70% 0%;
  }
  @media (max-width: 600px) {
    background: url(#{$backgroundUrlMobile});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 70% 0%;
  }
}
.textBlock {
  height: auto !important;
  position: relative;
  z-index: 1;
  display: flex;
  gap: 25px;
  flex-direction: column;
  max-width: 557px;
  width: calc(100% - 226px);
  height: 100%;
  padding: 66px 0 66px 60px;
  @media (max-width: 768px) {
    width: calc(100% - 120px);
    padding: 66px 0 66px 40px;
  }
  @media (max-width: 600px) {
    width: calc(100% - 60px);
  }
  h4,
  p {
    padding: 0;
    margin: 0;
  }
}
.title {
  font-weight: 1000;
  font-size: 60px;
  line-height: 55px;
  letter-spacing: 0.03em;
  color: #2c233d;
  margin-bottom: 25px;
}
.text {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.03em;
  color: #2c233d;
}

@media (max-width: 600px) {
  .textBlock {
    padding: 29px 0 10px 20px;
    gap: 8px;
    max-width: 305px;
  }
  .title {
    font-size: 30px;
    line-height: 27px;
  }
  .text {
    font-size: 13px;
    line-height: 16px;
  }
}
