$backgroundUrl: "../../assets/banners/levels.png";
$backgroundUrlMobile: "../../assets/banners/levelsMobile.png";

.root {
  border-radius: 20px;

  display: flex;
  justify-content: space-between;
  overflow: hidden;
  height: 100%;

  position: relative;
}
.backgroundOverlay {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
  background: url(#{$backgroundUrl});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  @media (max-width: 1440px) {
    background-position: 75%;
  }
  @media (max-width: 600px) {
    background: url(#{$backgroundUrlMobile});
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: 100%;
  }
}
.rakeRoot {
  height: auto !important;
  position: relative;
  z-index: 1;
  padding: 20px;
  padding-right: 0;
  width: 100%;
}
.title {
  font-weight: 1000;
  font-size: 44px;
  line-height: 44px;
  letter-spacing: 0.03em;

  color: #fcd517;
  margin-bottom: 18px;
  white-space: nowrap;
  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 27px;
    margin-bottom: 7px;
  }
  display: inline-block;
}
.text {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.03em;
  width: 70%;
  color: #ffffff;

  @media (max-width: 600px) {
    font-size: 13px;
    line-height: 16px;
    display: inline-block;
  }
}
