$borderSize: 2px;

.root {
  border-radius: 20px;
  background: linear-gradient(90deg, #000046 0%, #1cb5e0 100%);

  display: flex;
  justify-content: space-between;
  overflow: hidden;
  height: 100%;

  position: relative;
}
.backgroundOverlay {
  position: absolute;
  z-index: 0;
  height: 100%;
  width: 100%;
}

.rakeRoot.rakeRoot {
  height: auto !important;
  position: relative;
  z-index: 1;
  padding: 20px;
  padding-right: 0;
}
.title.title {
  font-weight: 900;
  font-size: 48px;
  line-height: 44px;
  letter-spacing: 0.03em;

  color: #ff9900;

  margin-right: 100px;
  margin-bottom: 8px;
  white-space: nowrap;
  @media (max-width: 600px) {
    font-size: 30px;
    line-height: 27px;
    margin-bottom: 5px;
  }
}
.text.text {
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.03em;
  width: calc(100% - 85px);
  color: #ffffff;

  @media (max-width: 600px) {
    font-size: 13px;
    line-height: 16px;
    display: inline-block;
  }
}
.link {
  color: #fcd517;
}

.buttonWrapper {
  position: absolute;
  z-index: 2;
  top: 54%;
  width: 100%;
  left: 0;
  padding-left: 20px;

  display: grid;
  grid-template-columns: 180px 150px;
  grid-template-rows: 38px;
  gap: 20px;

  @media (max-width: 600px) {
    grid-template-columns: 156px 132px;
    grid-template-rows: 33px;
    gap: 10px;
  }
  @media (max-width: 345px) {
    grid-template-columns: 56% 40%;
    grid-template-rows: 33px;
    gap: 5px;
  }
}
.wrapperWrap {
  top: 40%;
}
.wrapInput {
}

.input {
  // max-width: 180px;
  width: 100%;
  height: 100%;
  padding: 0 12px;
  // margin-right: 20px;
  font-family: "Gilroy";
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  background: rgba(24, 24, 34, 0.5);
  border-radius: 6.38183px;
  outline: none;
  color: rgba(255, 255, 255, 0.5);
  border: 1px solid #ffe600;

  &::placeholder {
  }
  @media (max-width: 600px) {
    // max-width: 156px;
    width: 100%;
    // height: 33px;
    // margin-right: 10px;

    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
  }
}

.wrapper {
}
.button {
  position: relative;

  // max-width: 150px;
  width: 100%;
  height: 100%;
  border-radius: 19.1455px;

  font-weight: 700;
  font-size: 14px;
  color: #010248;
  font-family: "Gilroy";
  z-index: 1;
  letter-spacing: 0.03em;
  text-transform: uppercase;

  &::before {
    content: "";
    background: linear-gradient(360deg, #ff9900 10.91%, #ffe600 100%);
    border-radius: 19.1455px;

    height: 100%;
    width: 100%;

    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  @media (max-width: 600px) {
    // max-width: 132px;
    // height: 33px;
    font-weight: 700;
    font-size: 12px;
    line-height: 11px;
  }
}
.img {
  margin-bottom: 17px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
